export default {
    init: function (context) {
        let method = context.$store.getters.getMethod;
        if (method === 'addTask') {
            return {
                title: ' ',
                description: ' ',
                singleLineInput: false,
                multiLineInput: true,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.addTask
            }
        }
        if (method === 'deleteColumn') {
            return {
                title: ' ',
                description: '     ?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.deleteColumn
            }
        }
        if (method === 'addGroup') {
            return {
                title: '  ',
                description: '  :',
                singleLineInput: true,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.addGroup
            }
        }
        if (method === 'deleteGroup') {
            return {
                title: '  ',
                description: '      ?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.deleteGroup
            }
        }
        if (method === 'ungroup') {
            return {
                title: '',
                description: '      ?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.ungroup
            }
        }
        if (method === 'changeTitle') {
            return {
                title: ' ',
                description: '  :',
                singleLineInput: true,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.changeTitle
            }
        }
        if (method === 'editTask') {
            return {
                title: ' ',
                description: '  :',
                singleLineInput: false,
                multiLineInput: true,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.editTask
            }
        }
        if (method === 'deleteTask') {
            return {
                title: ' ',
                description: '     ?',
                singleLineInput: false,
                multiLineInput: false,
                toggle: false,
                cancel: context.closeDialogPanel,
                ok: context.deleteTask
            }
        }
        if (method === 'editColumn') {
            return {
                title: ' ',
                description: ' :',
                singleLineInput: true,
                multiLineInput: false,
                toggle: true,
                cancel: context.closeDialogPanel,
                ok: context.editColumn
            }
        }
    }
}
