export default {
    state: {
        isOpen: false,
        payload: {}
    },
    mutations: {
        openDialogPanel(state, payload) {
            state.isOpen = true;
            state.payload = payload;
        },
        closeDialogPanel(state) {
            state.isOpen = false;
        }
    },
    actions: {
        openDialogPanel({commit}, payload) {
            commit('openDialogPanel', payload);
        },
        closeDialogPanel({commit}) {
            commit('closeDialogPanel');
        }
    },
    getters: {
        isOpen(state) {
            return state.isOpen
        },
        getMethod(state) {
            return state.payload.method
        },
        getData(state) {
            return state.payload
        }
    }
}

