<template>
    <div>
        <div class="panel">
            <div class="panel__header">
                <h1 class="title">{{viewElements.title}}</h1>
            </div>

            <div class="panel__main">
                <div class="panel__field">
                    <label class="field__title">{{viewElements.description}}</label>
                    <input v-if="viewElements.singleLineInput" class="field__input" name="msg" v-model="singleLineInput" autocomplete="off"/>
                    <textarea v-if="viewElements.multiLineInput" name="msg" cols="25" rows="3" v-model="multiLineInput"></textarea>
                    <div v-if="viewElements.toggle" class="toggleCounter">
                        <label class="toggleCounter__title">  </label>
                        <div class="toggleCounter__main">
                            <section title=".slideThree">
                                <div class="slideThree">
                                    <input type="checkbox" value="None" id="slideThree" name="check" v-model="checked"/>
                                    <label for="slideThree"></label>
                                </div>
                            </section>
                            <label class="max-counter__title">:</label>
                            <input type="text" class="max-counter__input" v-model="maxSize"
                                   :class="{'max-counter__input--disabled': !checked}">
                        </div>
                    </div>
                </div>
                <div class="panel__buttons">
                    <button
                            class="button"
                            @click="viewElements.cancel"
                    >
                        
                    </button>
                    <button
                            class="button"
                            @click="viewElements.ok"
                    >
                        
                    </button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import ViewElements from './ViewElements'
    export default {
        data () {
            return {
                singleLineInput: this.$store.getters.getData.title,
                multiLineInput: this.$store.getters.getData.text,
                maxSize: this.$store.getters.getData.maxSize,
                checked: this.$store.getters.getData.checked,
                viewElements: ViewElements.init(this)
            }
        },
        methods: {
            closeDialogPanel() {
                this.$store.dispatch('closeDialogPanel');
            },
            addTask() {
                let payload = this.$store.getters.getData;
                let text = this.multiLineInput;
                if (text) {
                    this.$store.dispatch('addTask', {...payload, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteColumn() {
                let payload = this.$store.getters.getData;
                this.$store.dispatch('deleteColumn', payload);
                this.$store.dispatch('closeDialogPanel');
            },
            addGroup() {
                let payload = this.$store.getters.getData;
                let text = this.singleLineInput;
                if (text) {
                    this.$store.dispatch('addGroup', {...payload, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteGroup() {
                let payload = this.$store.getters.getData;
                this.$store.dispatch('deleteGroup', payload);
                this.$store.dispatch('closeDialogPanel');
            },
            ungroup() {
                let payload = this.$store.getters.getData;
                this.$store.dispatch('ungroup', payload);
                this.$store.dispatch('closeDialogPanel');
            },
            changeTitle() {
                let payload = this.$store.getters.getData;
                let text = this.singleLineInput;
                if (text) {
                    this.$store.dispatch('changeTitle', {...payload, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            editTask() {
                let payload = this.$store.getters.getData;
                let text = this.multiLineInput;
                if (text) {
                    this.$store.dispatch('editTask', {...payload, text});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            editColumn() {
                let payload = this.$store.getters.getData;
                let text = this.singleLineInput;
                let maxSize = this.maxSize;
                let checked = this.checked;
                if (text) {
                    this.$store.dispatch('editColumn', {...payload, text, maxSize, checked});
                    this.$store.dispatch('closeDialogPanel');
                }
            },
            deleteTask() {
                let payload = this.$store.getters.getData;
                this.$store.dispatch('deleteTask', payload);
                this.$store.dispatch('closeDialogPanel');
            }
        }
    }
</script>

