    $activeColor: #65e0b4; //green
    $darkenColor: darken($activeColor, 20%);
    .slideThree {
        width: 80px;
        height: 26px;
        background: #333;
        position: relative;
        border-radius: 50px;
        box-shadow: inset 0 1px 1px rgba(0,0,0,0.5), 0 1px 0 rgba(255,255,255,0.2);
        &:after {
            content: 'OFF';
