<template>
    <div>
        <div class="column">
            <div class="column__header">
                <span @click="openDialogPanel({method: 'editColumn',
                                               columnId: column.id,
                                               place: 'column',
                                               title: column.title,
                                               checked: column.checked,
                                               maxSize: column.maxSize
                                               })"
                      class="column__title"
                >
                    <span>{{column.title}} </span>
                    <span :class="{'column__counter--red': exceeded(column.id) && checked({columnId: column.id})}">
                        <span>( {{ countTasks({columnId: column.id}) }}</span>
                        <span v-if="checked({columnId: column.id})"> / {{ maxSize({columnId: column.id}) }}</span> )
                    </span>
                </span>
            </div>

            <div class="column__main">
                <div v-for="task in column.tasks" v-bind:key="task.id">
                    <div v-if="task.type === 'task'">
                        <Task class="task-item" v-bind:task="task" v-bind:columnId="column.id"></Task>
                    </div>
                    <div v-if="task.type === 'group'">
                        <Group class="task-item" v-bind:group="task" v-bind:columnId="column.id"></Group>
                    </div>
                </div>
            </div>

            <div class="column__footer">
                <font-awesome-icon
                        icon="plus"
                        class="btn btn--group"
                        @click="openDialogPanel({method: 'addTask', columnId: column.id, place: 'column'})"
                />
                <font-awesome-icon
                        icon="trash"
                        class="btn btn--group"
                        @click="openDialogPanel({method: 'deleteColumn', columnId: column.id})"
                />
                <font-awesome-icon
                        icon="folder-plus"
                        class="btn btn--group"
                        @click="openDialogPanel({method: 'addGroup', columnId: column.id, place: 'column'})"
                />
            </div>

        </div>
    </div>
</template>

<script lang="js">
    import Group from './Group'
    import Task from './Task'

    export default {
        name: 'Column',
        props: ['column'],
        components: {
            Group,
            Task
        },
        beforeMount() {
            this.initCount()
        },
        data () {
            return {
                isEdit: false,
                edited: ' '
            }
        },
        methods: {
            openDialogPanel(payload) {
                this.$store.dispatch('openDialogPanel', payload);
            },
            addTask(payload) {
                this.$store.dispatch('addTask', payload)
            },
            deleteColumn(payload) {
                this.$store.dispatch('deleteColumn', payload);
            },
            addGroup(payload) {
                this.$store.dispatch('addGroup', payload);
            },
            countTasks(payload) {
                return this.$store.getters.countTasks(payload);
            },
            maxSize(payload) {
                return this.$store.getters.maxSize(payload);
            },
            checked(payload) {
                return this.$store.getters.checked(payload);
            },
            initCount() {
                this.$store.dispatch('countTasks');
            },
            exceeded(id) {
                return this.countTasks({columnId: id}) > this.maxSize({columnId: id})
            }
        },
        computed: {

        }
    }
</script>

